.vacancy-card {
  max-width: 100%;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid black;
  border-radius: 20px;
  background-color: var(--primary-bg-color);
  flex-grow: 1;
}

.vacancy-card__text {
  margin: 0;
  font-family: 'YS_Text';
  font-style: normal;
  color: var(--primary-text-color);
}

.vacancy-card:nth-child(2n){
  flex-grow: 2;
}

.vacancy-card:nth-child(1n) .vacancy-card__vacancy-course {
  max-width: 510px;
}

.vacancy-card:nth-child(2n) .vacancy-card__vacancy-course {
  width: 100%;
}

.vacancy-card__vacancy-name {
  font-weight: 400;
  font-size: var(--l-text-size);
  line-height: var(--l-line-height);
}

.vacancy-card__vacancy-course {
  margin-top: 10px;
  font-weight: 700;
  font-size: var(--xl-text-size);
  line-height: var(--xl-line-height);
}

.vacancy-card__vacancy-direction {
  margin-top: 25px;
  /* width: 100%; */
  font-weight: 400;
  font-size: var(--l-text-size);
  line-height: var(--l-line-height);
  color: var(--transparent-text-color);
}

.vacancy-card__footer {
}

.vacancy-card__footer-link-container {
  margin-top: 5px;
  height: 35px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.vacancy-card__link {
  height: 100%;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}

